html {
  font-family: 'PT Sans', sans-serif;
}

@mixin os() {
  font-family: 'Open Sans Condensed', sans-serif;
}

@mixin rs() {
  font-family: 'Roboto Slab', serif;
}

@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon.eot?fiulni');
  src:  url('../fonts/icomoon.eot?fiulni#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?fiulni') format('truetype'),
    url('../fonts/icomoon.woff?fiulni') format('woff'),
    url('../fonts/icomoon.svg?fiulni#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

.icon {
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
}

.icon-phone:before {
  content: "\e900";
}
.icon-whatsapp:before {
  content: "\e901";
}
.icon-telegram:before {
  content: "\e902";
}

