$mobile: 'screen and (max-width: 767px)';
$desktop: 'screen and (min-width: 768px)';

.u-title {
  font-size: 32px;
  font-weight: 300;
  padding: 50px 0 35px;
  @include rs();

  @media #{$mobile} {
    font-size: 22px;
    padding: 0 10px;
  }
}

.wrapper {
  max-width: 940px;
  margin: 0 auto;
  padding: 0 30px;

  @media #{$mobile} {
    padding: 0 15px;
  }
}

.main-screen {
  position: relative;
  display: table;
  width: 100%;
  min-height: 100%;
  background: url(../images/background.jpg) center / cover;

  > .scroll-link {
    display: inline-block;
    position: absolute;
    z-index: 4;
    bottom: 40px;
    left: 50%;
    margin-left: -20px;
    width: 40px;
    height: 40px;
    background: url(../images/arrow_down.svg) center / 80% no-repeat;
    opacity: 0.65;
    transition: all .2s ease;

    &:hover {
      opacity: 1;
    }
  }

  .nav {
    position: absolute;
    z-index: 4;
    top: 80px;
    left: 60px;
    right: 60px;
    font-size: 18px;
    text-align: center;

    @media #{$mobile} {
      left: 20px;
      right: 20px;
      top: 35px;
      font-size: 13px;
    }

    a {
      color: rgba(255, 255, 255, .5);
      text-decoration: none;
      transition: all .6s cubic-bezier(0.165, 0.84, 0.44, 1);

      &:hover {
        color: rgba(255, 255, 255, .75);
      }

      @media #{$mobile} {
        color: rgba(255, 255, 255, .75);

        &:hover {
          color: rgba(255, 255, 255, 1);
        }
      }
    }

    > span {
      display: inline-block;
      margin: 0 7px;
      color: rgba(255, 255, 255, .5);
    }
  }

  .shadow {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, .25);
  }

  .gradient {
    position: absolute;
    z-index: 2;
    bottom: 0;
    left: 0;
    right: 0;
    height: 50%;
    background: linear-gradient(to bottom, rgba(0,0,0,0) 0%, rgba(0,0,0,.75) 50%,rgba(0,0,0,1) 100%);
  }

  .border {
    position: absolute;
    z-index: 1;
    top: 60px;
    left: 40px;
    right: 40px;
    bottom: 0;
    border: 1px solid rgba(255, 255, 255, .5);
    border-bottom: none;

    @media #{$mobile} {
      top: 20px;
      left: 10px;
      right: 10px;
    }
  }

  .curtain {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 4;
    pointer-events: none;
  }

  .wrapper {
    position: relative;
    top: 20px;
    display: table-cell;
    vertical-align: middle;
    position: relative;
    z-index: 3;
    color: #fff;
    text-align: center;

    @media #{$mobile} {
      top: 0;
    }

    .image {
      &:before {
        content: "";
        display: inline-block;
        vertical-align: middle;
        width: 200px;
        height: 200px;
        background: url(../images/brand-icon.svg) center / contain;

        @media #{$mobile} {
          width: 100px;
          height: 100px;
        }
      }
    }

    .brand {
      @include rs();
      font-weight: 700;
      font-size: 48px;
      text-transform: uppercase;
      margin-top: 25px;

      @media #{$mobile} {
        font-size: 32px;
        margin-top: 12.5px;
      }
    }

    .title {
      font-size: 32px;
      font-weight: 300;
      margin: 0;
      color: #e1be86;
      @include os();

      @media #{$mobile} {
        font-size: 22px;
      }
    }

    .city {
      margin-top: 20px;
      font-size: 14px;
      color: rgba(255, 255, 255, .25);

      @media #{$mobile} {
        margin-top: 10px;
      }
    }
  }
}

.desc-screen {
  background: #000;
  overflow: hidden;
  color: #fff;
  text-align: center;

  .about {
    font-size: 22px;
    max-width: 640px;
    margin: 0 auto;
    padding: 0 0;
    line-height: 1.4;

    @media #{$mobile} {
      font-size: 18px;
      padding: 0 10px;
    }

    .menu-link {
      margin-top: 1.5em;

      a {
        display: inline-block;
        color: #fff;
        text-decoration: none;

        span {
          border-bottom: 1px solid rgba(255, 255, 255, 0);
          transition: all .4s ease;
        }

        &:before {
          content: "";
          display: inline-block;
          vertical-align: middle;
          margin-right: .5em;
          width: 1.5em;
          height: 1.5em;
          background: url(../images/menu.svg) center / contain no-repeat;
        }

        &:hover {
          span {
            border-bottom: 1px solid rgba(255, 255, 255, 1);
          }
        }
      }
    }
  }

  .about-hr {
    .inner {
      max-width: 640px;
      margin: 0 auto;
      height: 1px;
      background: radial-gradient(ellipse at center, rgba(255,255,255,.5) 0%,rgba(255,255,255,0) 80%); 

      @media #{$mobile} {
        width: 90%;
      }
    }
  }

  .title {
    color: #e1be86;
    padding: 50px 0 35px;

    @media #{$mobile} {
      padding: 25px 15px 20px;
    }
  }

  .desc {
    font-size: 18px;
    padding-bottom: 100px;
    line-height: 1.6;

    @media #{$mobile} {
      font-size: 14px;
      padding-bottom: 50px;
    }
  }
}

.js-show {
  @media #{$desktop} {
    .inner {
      opacity: 0;
      transform: translateY(25px);
      transition: all 1.5s cubic-bezier(0.165, 0.84, 0.44, 1);
    }

    &.isShown {
      .inner {
        transform: translateY(0);
        opacity: 1;
      }
    }
  }
}

.benefits-screen {
  position: relative;
  // background: url(../images/footer.jpg) bottom center / cover no-repeat;
  text-align: center;
  padding-bottom: 150px;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, .5);

  @media #{$mobile} {
    padding-bottom: 100px;
  }

  .shadow {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, .6);
  }

  .wrapper {
    position: relative;
    z-index: 1;
  }

  .title {
    @include rs();
    font-size: 78px;
    color: #fff;
    padding: 125px 0 50px;

    @media #{$mobile} {
      font-size: 39px;
      padding: 70px 0 25px;
    }
  }

  .contact {
    font-size: 28px;
    margin-top: 35px;

    @media #{$mobile} {
      margin-top: 35px;
      font-size: 20px;
    }

    .sub {
      font-size: 12px;
      color: rgba(255, 255, 255, .5);
      margin-bottom: 5px;

      @media #{$mobile} {
        margin-bottom: 0;
      }
    }

    a {
      display: inline-block;
      vertical-align: middle;
      color: #fff;
      text-decoration: none;

      span {
        display: inline-block;
        vertical-align: middle;
        margin-right: 15px;
        
        &:before {
          transition: all .6s ease;
        }

        &.icon-phone:before {
          color: #00e676;
        }

        &.icon-telegram:before {
          color: #2aa1da;
        }
      }

      &:hover {
        span {
          &:before {
            color: #fff;
          }
        }
      }
    }
  }

  .socials {
    margin-top: 25px;

    a {
      color: #fff;
      font-size: 26px;
      text-decoration: none;
      margin: 0 25px;
    }
  }
}

.about-desc {
  position: relative;
  margin-top: 75px;
  background: {
    size: cover;
    position: center;
    image: url(../images/break2.jpg);
  }

  @media #{$mobile} {
    margin-top: 50px;
  }

  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, .65);
  }

  .content {
    position: relative;
    z-index: 2;
    color: #fff;
    padding: 75px 0 125px;
    font-size: 18px;
    line-height: 1.6;

    @media #{$mobile} {
      font-size: 16px;
      padding: 40px 0 60px;
    }
  }

  .title {
    text-align: center;

    &:before {
      content: '';
      display: inline-block;
      height: 60px;
      width: 60px;
      background: url(../images/info.svg) center / contain no-repeat;

      @media #{$mobile} {
        height: 40px;
        width: 40px;
      }
    }
  }

  .desc {
    margin-top: 35px;
    text-shadow: 2px 2px 2px rgba(0, 0, 0, .5);

    @media #{$mobile} {
      margin-top: 20px;
    }
  }

  .icons {
    font-size: 32px;
    text-align: center;

    @media #{$mobile} {
      font-size: 20px;
    }
  }
}